import React, { useState } from "react";
import { useCart } from "react-use-cart";
import { Link } from "react-router-dom";

function CheckOut() {
  const { cartTotal, items, emptyCart } = useCart();

  const [formData] = useState({
    fullName: "",
    streetAddress: "",
    city: "",
    postalCode: "",
    paymentMethod: "cash",
  });

  const discountRate = 0.15;
  const shippingCost = 30;
  const totalAfterDiscount = cartTotal * (1 - discountRate);
  const finalTotal = totalAfterDiscount + shippingCost;

  return (
    <div className="section checkout">
      <div className="container">
        <h2 className="text-center mb-5">Checkout</h2>
        <div className="row">
          {/* Billing Form */}
          <div className="col-lg-6 col-md-12 mb-4">
            <div className="checkout-form card p-4">
              <h4 className="mb-4">Billing Details</h4>
              <form>
                <div className="mb-3">
                  <label htmlFor="fullName" className="form-label">
                    Full Name
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="fullName"
                    name="fullName"
                    value={formData.fullName}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="streetAddress" className="form-label">
                    Street Address
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="streetAddress"
                    name="streetAddress"
                    value={formData.streetAddress}
                    required
                  />
                </div>
                <div className="row">
                  <div className="col-md-6 mb-3">
                    <label htmlFor="city" className="form-label">
                      City
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="city"
                      name="city"
                      value={formData.city}
                      required
                    />
                  </div>
                  <div className="col-md-6 mb-3">
                    <label htmlFor="postalCode" className="form-label">
                      Postal Code
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="postalCode"
                      name="postalCode"
                      value={formData.postalCode}
                      required
                    />
                  </div>
                </div>

                <h4 className="mb-3">Payment Method</h4>
                <div className="mb-3">
                  {["cash", "credit-card", "paypal"].map((method) => (
                    <div className="form-check" key={method}>
                      <input
                        className="form-check-input"
                        type="radio"
                        name="paymentMethod"
                        id={method}
                        value={method}
                        checked={formData.paymentMethod === method}
                      />
                      <label className="form-check-label" htmlFor={method}>
                        {method === "cash"
                          ? "Cash on Delivery"
                          : method === "credit-card"
                          ? "Credit Card"
                          : "PayPal"}
                      </label>
                    </div>
                  ))}
                </div>
              </form>
            </div>
          </div>

          {/* Order Summary */}
          <div className="col-lg-6 col-md-12">
            <div className="order-summary card p-4">
              <h4 className="mb-4">Order Summary</h4>
              {items.length > 0 ? (
                <>
                  {items.map((item) => (
                    <div
                      className="d-flex justify-content-between mb-2"
                      key={item.id}
                    >
                      <span>
                        {item.title} (x{item.quantity})
                      </span>
                      <span>${(item.price * item.quantity).toFixed(2)}</span>
                    </div>
                  ))}

                  <hr />

                  <div className="summary-item d-flex justify-content-between">
                    <span>Subtotal</span>
                    <span>${cartTotal.toFixed(2)}</span>
                  </div>
                  <div className="summary-item d-flex justify-content-between">
                    <span>Discount</span>
                    <span>-15%</span>
                  </div>
                  <div className="summary-item d-flex justify-content-between">
                    <span>Shipping</span>
                    <span>${shippingCost.toFixed(2)}</span>
                  </div>

                  <hr />

                  <div className="d-flex justify-content-between fw-bold">
                    <span>Total</span>
                    <span>${finalTotal.toFixed(2)}</span>
                  </div>
                </>
              ) : (
                <p className="text-center">Your cart is empty.</p>
              )}

              <Link
                to="/confirmed"
                onClick={emptyCart}
                className="btn checkout-btn w-100 mt-3"
              >
                Place Order
              </Link>

              <Link
                to="/shop"
                className="btn btn-outline-secondary mt-4 w-100"
              >
                Continue Shopping
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CheckOut;
