/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { useCart } from "react-use-cart";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
function Cart() {
 const {
    isEmpty,
    totalUniqueItems,
    items,
    updateItemQuantity,
    removeItem,
    cartTotal,
  } = useCart();
  const navigate = useNavigate();

  if (isEmpty) {
    return (
      <div className="empty-cart-container">
        <img
          src="https://cdni.iconscout.com/illustration/free/thumb/free-empty-cart-4085814-3385481.png"
          alt="Empty Shopping Cart"
          className="empty-cart-image"
        />
        <h3 className="empty-cart-title">Your Shopping Cart is Empty</h3>
        <p className="empty-cart-message">You haven't added any products yet</p>
        <button
          className="empty-cart-button"
          onClick={() => navigate("/shop")}
        >
          Start Shopping Now
        </button>
      </div>
    );
  }

  return (
    <main className="cart-page">
      <section className="shopping-cart">
        <div className="cart-container">
          <div className="cart-header">
            <h2>Shopping Cart ({totalUniqueItems})</h2>
          </div>
          <div className="cart-content">
            <div className="cart-items-container">
              <div className="cart-items">
                {items.map((item) => (
                  <div className="cart-product" key={item.id}>
                    <div className="product-row">
                      <div className="product-image-container">
                        <img
                          className="product-image"
                          src={item.images[0]}
                          alt={item.title}
                        />
                      </div>
                      <div className="product-details">
                        <div className="product-info-row">
                          <div className="product-info">
                            <h3 className="product-title">
                              <a href="#">{item.title}</a>
                            </h3>
                            <div className="product-meta">
                              <div>
                                Brand: <span>{item.brand}</span>
                              </div>
                              <div>
                                Stock: <span>{item.stock}</span>
                              </div>
                              <div>
                                SKU: <span>{item.sku}</span>
                              </div>
                            </div>
                          </div>
                          <div className="product-quantity">
                            <label htmlFor={`quantity-${item.id}`}>Quantity:</label>
                            <input
                              id={`quantity-${item.id}`}
                              type="number"
                              min="1"
                              value={item.quantity}
                              onChange={(e) =>
                                updateItemQuantity(
                                  item.id,
                                  parseInt(e.target.value)
                                )
                              }
                              className="quantity-input"
                            />
                            <button
                              className="remove-button"
                              onClick={() => removeItem(item.id)}
                            >
                              Remove
                            </button>
                          </div>
                          <div className="product-price">
                            <span>${(item.price * item.quantity).toFixed(2)}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            <div className="cart-summary">
              <div className="summary-content">
                <h3>Order Summary</h3>
                <div className="summary-item">
                  <span className="summary-label">Subtotal</span>
                  <span className="summary-value">${cartTotal.toFixed(2)}</span>
                </div>
                <div className="summary-item">
                  <span className="summary-label">Discount</span>
                  <span className="summary-value">15.0%</span>
                </div>
                <div className="summary-item">
                  <span className="summary-label">Shipping</span>
                  <span className="summary-value">30.0$</span>
                </div>
                <div className="summary-item total">
                  <span className="summary-label">Total</span>
                  <span className="summary-value">
                    ${((cartTotal * 0.85) + 30).toFixed(2)}
                  </span>
                </div>
                <Link to='/checkout' className="checkout-button">Proceed to Checkout</Link>
              
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default Cart