/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext, useState } from "react";
import { Link, NavLink } from "react-router-dom";
import { useCart } from "react-use-cart";
import { CartProvider } from "react-use-cart";
import { SearchContext } from "./SearchContext";

function ShopHeader() {
  const { setSearch } = useContext(SearchContext);
  const { totalItems } = useCart();
  const [showSearch, setShowSearch] = useState(false);

  function searchResult(event) {
    setSearch(event.target.value);
  }

  function clearSearch() {
    setSearch(""); 
    setShowSearch(false); 
  }

  return (
    <CartProvider>
      <nav className="header-navbar navbar navbar-expand-lg">
        <div className="container-fluid d-flex justify-content-between align-items-center">
          <NavLink className="navbar-brand header-brand ms-2" to="/">
            DUMMY&#8482;
          </NavLink>

          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#headerNav"
            aria-controls="headerNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>

          <div
            className="align-self-center collapse navbar-collapse mx-auto d-lg-flex justify-content-lg-between"
            id="headerNav"
          >
            <ul className="nav navbar-nav d-flex justify-content-center flex-grow-1 mx-lg-auto">
              <li className="nav-item mx-2">
                <NavLink className="nav-link header-nav-link" to="/">
                  Home
                </NavLink>
              </li>
              <li className="nav-item mx-2">
                <NavLink className="nav-link header-nav-link" to="/shop">
                  Shop
                </NavLink>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link header-nav-link" to="/about">
                  About
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link header-nav-link" to="/contact-us">
                  Contact
                </Link>
              </li>
            </ul>
            <div className="header-icons d-flex justify-content-center align-items-center">
              <div className="header-search d-flex align-items-center gap-2">
                <Link
                  className="header-search-icon mx-3"
                  to="#"
                  onClick={(e) => {
                    e.preventDefault();
                    setShowSearch((prev) => !prev);
                  }}
                >
                  <i className="fa fa-fw fa-search" />
                </Link>
                {showSearch && (
                  <div className="input-group">
                    <input
                      type="text"
                      className="form-control header-search-input"
                      placeholder="Search for products..."
                      onChange={searchResult}
                      style={{ maxWidth: "200px", transition: "0.3s" }}
                    />
                    <button
                      className="btn btn-outline-secondary"
                      type="button"
                      onClick={clearSearch}
                    >
                      <i className="fa fa-fw fa-times" />
                    </button>
                  </div>
                )}
              </div>
              <NavLink
                className="header-icon position-relative text-decoration-none mx-3"
                to="/user"
              >
                <i className="fa fa-fw fa-user" />
              </NavLink>
              <NavLink
                className="header-icon position-relative text-decoration-none mx-3"
                to="/cart"
              >
                <i className="fa fa-fw fa-cart-arrow-down" />
                <span className="header-cart-badge position-absolute top-0 start-100 translate-middle badge rounded-pill">
                  {totalItems}
                </span>
              </NavLink>
            </div>
          </div>
        </div>
      </nav>
    </CartProvider>
  );
}

export default ShopHeader;