import React, { useContext, useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useCart } from "react-use-cart";
import { apiValue } from "../Data/AllData";
import { Helmet } from "react-helmet";
import { Carousel } from "react-bootstrap";


const ProductDetails = () => {
  const { id } = useParams();
  const { addItem } = useCart();
  const data = useContext(apiValue);
  const [product, setProduct] = useState(null);

  useEffect(() => {
    const foundProduct = data.find((item) => item.id === parseInt(id));
    if (foundProduct) {
      setProduct(foundProduct);
    }
  }, [id, data]);

  if (!product) {
    return <div className="loading">Loading product...</div>;
  }

  return (
    <>
      <Helmet>
        <title>{product.title} | Your Store</title>
        <meta name="description" content={product.description} />
      </Helmet>

      <div className="product-details-page">
        <div className="product-details-container">
          <div className="product-image">
            <Carousel>
              {product.images?.map((image, index) => (
                <Carousel.Item key={index}>
                  <img src={image} alt={`${product.title} ${index + 1}`} />
                </Carousel.Item>
              ))}
            </Carousel>
          </div>

          <div className="product-info">
            <h1 className="product-titleD">{product.title}</h1>
            <hr />
            <p className="product-priceD">${product.price?.toFixed(2)}</p>
            <p className="product-discount">
              <i className="fas fa-tags"></i> {product.discountPercentage}% OFF
            </p>
            <hr />
            <p className="product-description">{product.description}</p>

            <div className="product-rating">
              <span className="star">
                {"".repeat(Math.round(product.rating || 0)) +
                  "".repeat(5 - Math.round(product.rating || 0))}
              </span>
              <span>
                ({product.rating?.toFixed(1)} / 5,{" "}
                {product.reviews?.length || 0} reviews)
              </span>
            </div>

            <div className="product-details">
              <p>
                <i className="fas fa-box"></i> <strong>Category:</strong>{" "}
                {product.category}
              </p>
              <p>
                <i className="fas fa-tag"></i> <strong>Brand:</strong>{" "}
                {product.brand}
              </p>
              {product.sku && (
                <p>
                  <i className="fas fa-barcode"></i> <strong>SKU:</strong>{" "}
                  {product.sku}
                </p>
              )}
              {product.weight && (
                <p>
                  <i className="fas fa-weight-hanging"></i>{" "}
                  <strong>Weight:</strong> {product.weight}g
                </p>
              )}
              {product.dimensions && (
                <p>
                  <i className="fas fa-ruler-combined"></i>{" "}
                  <strong>Dimensions:</strong> {product.dimensions.width}x
                  {product.dimensions.height}x{product.dimensions.depth} cm
                </p>
              )}
              {product.warrantyInformation && (
                <p>
                  <i className="fas fa-shield-alt"></i>{" "}
                  <strong>Warranty:</strong> {product.warrantyInformation}
                </p>
              )}
              {product.shippingInformation && (
                <p>
                  <i className="fas fa-shipping-fast"></i>{" "}
                  <strong>Shipping:</strong> {product.shippingInformation}
                </p>
              )}
              {product.availabilityStatus && (
                <p>
                  <i className="fas fa-check-circle"></i>{" "}
                  <strong>Availability:</strong> {product.availabilityStatus}
                </p>
              )}
              {product.returnPolicy && (
                <p>
                  <i className="fas fa-undo"></i>{" "}
                  <strong>Return Policy:</strong> {product.returnPolicy}
                </p>
              )}
              {product.tags && product.tags.length > 0 && (
                <p>
                  <i className="fas fa-tags"></i> <strong>Tags:</strong>{" "}
                  {product.tags.join(", ")}
                </p>
              )}
            </div>

            <div className="product-buttonsD">
              <button onClick={() => addItem(product)} className="product-btnD">
                <i className="fas fa-cart-plus"></i> Add to Cart
              </button>
              <button className="product-btn-secondaryD">
                <i className="fas fa-heart"></i> Add to Wishlist
              </button>
            </div>

            <hr />
            {product.reviews && product.reviews.length > 0 && (
              <div className="product-reviews">
                <h2>Customer Reviews</h2>
                {product.reviews.map((review, index) => (
                  <div key={index} className="review">
                    <p className="review-rating">
                      {"".repeat(review.rating) +
                        "".repeat(5 - review.rating)}
                    </p>
                    <p className="review-comment">{review.comment}</p>
                    <p className="review-meta">
                      By {review.reviewerName} on{" "}
                      {new Date(review.date).toLocaleDateString()}
                    </p>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductDetails;
