/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext, useState } from "react";
import { apiValue } from "../Data/AllData";
import { useCart } from "react-use-cart";
import { SearchContext } from "../Components/SearchContext";
import { CartProvider } from "react-use-cart";
import { Link } from "react-router-dom";

function Shop() {
  const { search } = useContext(SearchContext);
  const data = useContext(apiValue);
  const { addItem } = useCart();
  const [selectedCategory, setSelectedCategory] = useState("all");

  // Define categories
  const categories = [
    { id: "all", title: "All" },
    { id: "beauty", title: "Beauty" },
    { id: "fragrances", title: "Fragrances" },
    { id: "furniture", title: "Furniture" },
    { id: "groceries", title: "Groceries" },
  ];

  // Filter products by category and search term
  const filteredData = data
    .filter((item) =>
      selectedCategory === "all"
        ? true
        : item.category.toLowerCase() === selectedCategory.toLowerCase()
    )
    .filter((item) =>
      search.trim() === ""
        ? true
        : item.title.toLowerCase().includes(search.toLowerCase())
    );

  return (
    <CartProvider>
      <div className="shop-section">
        <div className="container">
          {/* Category Buttons */}
          <div className="d-flex justify-content-center mb-4 flex-wrap gap-2">
            {categories.map((category) => (
              <button
                key={category.id}
                className={`btn ${
                  selectedCategory === category.id
                    ? "btn-primary"
                    : "btn-outline-primary"
                }`}
                onClick={() => setSelectedCategory(category.id)}
              >
                {category.title}
              </button>
            ))}
          </div>

          {/* Products */}
          <div className="row products-box">
            {filteredData.length > 0 ? (
              filteredData.map((item) => (
                <div
                  className="col-lg-4 col-md-6 align-self-center mb-30 products-items adv"
                  key={item.id}
                >
                  <div className="shop-item">
                    <Link to={`/product/${item.id}`}>
                      <img
                        src={item.images[0]}
                        alt={item.title}
                        className="shop-item-img"
                      />
                    </Link>

                    <span className="shop-discount">
                      {"-" + item.discountPercentage.toFixed(1) + "%"}
                    </span>
                    <h6 className="shop-price">${item.price}</h6>
                    <h4 className="shop-title">
                      <Link to={`/product/${item.id}`}>{item.title}</Link>
                    </h4>

                    <ul className="shop-details">
                      <li>
                        Category: <span>{item.category}</span>
                      </li>
                      <li>
                        Brand: <span>{item.brand || "Wmart"}</span>
                      </li>
                      <li>
                        Rating: <span>{item.rating + "/5"}</span>
                      </li>
                    </ul>
                    <div className="main-button">
                      <button
                        className="shop-add-btn"
                        onClick={() => addItem(item)}
                      >
                        Add to cart
                      </button>
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <p className="shop-no-results">No matching products found.</p>
            )}
          </div>
        </div>
      </div>
    </CartProvider>
  );
}

export default Shop;
