/* eslint-disable jsx-a11y/heading-has-content */
import React, { useRef } from "react";
import { Link, useNavigate } from "react-router-dom";

function Login() {
  const email = useRef();
  const pass = useRef();
  const navigate = useNavigate();

  function Check(e) {
    e.preventDefault();
    if (
      email.current.value == localStorage.getItem("email") &&
      pass.current.value == localStorage.getItem("Pass")
    ) {
      navigate('/confirmed')
    } else {
      document.getElementById("message").innerText='Enter your Email and your password !';
    }
  }

  return (
    <>
      <div className="login-page">
        <div className="login-container">
          <div className="login-header">
            <h1>Welcome Back !</h1>
            <h2 className="text-danger " id="message"></h2>
          </div>
          <div className="login-form-container">
            <form>
              <div className="login-form-group">
                <label htmlFor="email">Email</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  ref={email}
                  placeholder="Enter your Email"
                  required
                />
              </div>
              <div className="login-form-group">
                <label htmlFor="password">Password</label>
                <input
                  type="password"
                  id="password"
                  name="password"
                  ref={pass}
                  placeholder="Enter your password"
                  required
                />
              </div>
              <div className="login-form-group">
                <button
                  type="submit"
                  className="login-submit-btn"
                  onClick={Check}
                  id="send"
                >
                  Sign In
                </button>
              </div>
            </form>
          </div>
          <div className="login-footer">
            <p>
              Don't have an account? <Link to="/register">Create account</Link>
            </p>
            <Link to="/reset-password" className="login-forgot-password">
              Forgot password?
            </Link>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
