import React from 'react';
import { Link } from 'react-router-dom';
import { useEffect } from 'react';
let illustrationImg = 'assets/img/Illustration.png';

function User() {
   useEffect(() => {
      window.scrollTo(0, 0);
    }, []); 
  return (
    <div className="landing-page">
      <main className="landing-explore-page">
        <section className="landing-illustration-img">
          <img
            src={illustrationImg}
            alt="Financial management app interface"
          />
        </section>
        <section className="landing-illustration-text">
          <h1 className="landing-illustration-heading">Your Shopping Journey Starts Here !!</h1>
        </section>
        <section className="landing-illustration-buttons">
          <div className="landing-buttons-container">
            <Link to="/login" className="landing-btn" data-testid="login-button">
              Log In
            </Link>
            <Link to="/register" className="landing-btn-secondary" data-testid="register-button">
              Create account
            </Link>
          </div>
        </section>
      </main>
    </div>
  );
}

export default User;