import React from "react";
import { Link } from "react-router";

function Footer() {
  return (
    <footer className="p-5 d-flex align-items-center bg-dark text-white flex-column ">
      <div className="container d-flex justify-content-center align-items-center flex-column">
        <div className="top-footer w-100">
          <div className="row w-100">
            <div className="col-md-4 ">
              <h2 className="light-green border-light border-bottom pb-3">
                Dummy Shop
              </h2>

              <ul className="w-100 list-unstyled">
                <li className="py-2">
                  <i className="fa-solid fa-location-dot fs-5"></i>
                  <Link
                    to="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3415.790869186009!2d30.949560100000003!3d31.115544099999997!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14f7ab8605e22557%3A0x1b8aa7b8b961799f!2sMindset%20Training!5e0!3m2!1sar!2seg!4v1753967962198!5m2!1sar!2seg"
                    className="text-white"
                  >
                    Cairo , Egypt
                  </Link>
                </li>
                <li className="py-2">
                  <i className="fa-solid fa-phone fs-5"></i>
                  <Link to className="hover-success">
                    010-020-0340
                  </Link>
                </li>
                <li className="py-2">
                  <i className="fa-solid fa-envelope fs-5"></i>
                  <Link to className="hover-success">
                    info@company.com
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col-md-4 ">
              <h2 className="border-light border-bottom pb-3">Products</h2>

              <ul className="w-100 list-unstyled">
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Luxury
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Sport Wear
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Men's Shoes
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Women's Shoes
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Popular Dress
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Gym Accessories
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./shop"} className="hover-success">
                    Sport Shoes
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col-md-4 ">
              <h2 className="border-light border-bottom pb-3">Further Info</h2>

              <ul className="w-100 list-unstyled">
                <li className="py-2">
                  <Link to={"/"} className="hover-success">
                    Home
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./about"} className="hover-success">
                    About Us
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"./contact-us"} className="hover-success">
                    Shop Locations
                  </Link>
                </li>
                <li className="py-2">
                  <Link to={"/"} className="hover-success">
                    FAQs
                  </Link>
                </li>

                <li className="py-2">
                  <Link to={"./contact-us"} className="hover-success">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div className="middle-footer row border-top border-light w-100 align-items-center justify-content-between p-5">
        <ul className="list-inline  col-md-3 my-3 Footer-media">
          <li className="list-inline-item border border-light rounded-circle p-2 text-center">
            <Link
              className="text-light "
              target="_blank"
              to="http://facebook.com/"
            >
              <i className="fab fa-facebook-f fa-lg fa-fw"></i>
            </Link>
          </li>
          <li className="list-inline-item border border-light rounded-circle p-2 text-center">
            <Link
              className="text-light "
              target="_blank"
              to="https://www.instagram.com/"
            >
              <i className="fab fa-instagram fa-lg fa-fw"></i>
            </Link>
          </li>
          <li className="list-inline-item border border-light rounded-circle p-2 text-center">
            <Link
              className="text-light "
              target="_blank"
              to="https://twitter.com/"
            >
              <i className="fab fa-twitter fa-lg fa-fw"></i>
            </Link>
          </li>
          <li className="list-inline-item border border-light rounded-circle p-2 text-center">
            <Link
              className="text-light "
              target="_blank"
              to="https://www.linkedin.com/"
            >
              <i className="fab fa-linkedin fa-lg fa-fw"></i>
            </Link>
          </li>
        </ul>

        <div className="col-md-3">
          <div className="input-group mb-2 text-light">
            <input
              type="text"
              className="form-control bg-light border-light text-light "
              id="subscribeEmail"
              placeholder="Email address"
            />
            <div className="input-group-text bg-success text-light py-2 ">
              Subscribe
            </div>
          </div>
        </div>
      </div>
      <div className="b-footer w-100  bg-dark-black">
        <p className="ms-5">
          Copyright &copy; 2025 MindSet | Designed by Ahmed Ehab | Eslam Wael
        </p>
      </div>
    </footer>
  );
}

export default Footer;
