import { React, useRef, useState } from "react";
import { Link, useNavigate } from "react-router";
function Register() {
  const Fname = useRef();
  const Lname = useRef();
  const Pass = useRef();
  const PassCheck = useRef();
  const email = useRef();

  const [error, setError] = useState("");
  const navigate = useNavigate();

  const handleClick = (e) => {
    e.preventDefault();

    const fnameVal = Fname.current.value.trim();
    const lnameVal = Lname.current.value.trim();
    const passVal = Pass.current.value;
    const passCheckVal = PassCheck.current.value;
    const emailVal = email.current.value.trim();

    // Name validation: only letters, min 3 chars
    const nameRegex = /^[A-Za-z]{3,}$/;
    if (!nameRegex.test(fnameVal)) {
      setError(
        "First name must be at least 3 letters and contain only letters."
      );
      return;
    }
    if (!nameRegex.test(lnameVal)) {
      setError(
        "Last name must be at least 3 letters and contain only letters."
      );
      return;
    }

    // Password validation: min 8, at least one lowercase and one uppercase
    const passRegex = /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
    if (!passRegex.test(passVal)) {
      setError(
        "Password must be at least 8 characters and include at least one number, one lowercase letter, and one uppercase letter."
      );
      return;
    }

    // Password match
    if (passVal !== passCheckVal) {
      setError("Passwords do'nt match.");
      return;
    }

    // Email validation
    const emailRegex = /^[A-Za-z0-9._%+-]{3,}@gmail\.com$/;
    if (!emailRegex.test(emailVal)) {
      setError(
        "Email must be at least 3 characters before @ and end with @gmail.com."
      );
      return;
    }

    // If all validations pass
    setError("");
    localStorage.setItem("Fname", fnameVal);
    localStorage.setItem("Lname", lnameVal);
    localStorage.setItem("Pass", passVal);
    localStorage.setItem("PassCheck", passCheckVal);
    localStorage.setItem("email", emailVal);
    navigate("/login");
  };

  return (
    <>
      <div className="register-page">
        <div className="register-container">
          <div className="register-header">
            <h1>Create Account</h1>
            <p>Start Your Shopping Journey !</p>
          </div>
          {error && (
            <div
              style={{
                color: "red",
                marginBottom: "10px",
                textAlign: "center",
              }}
            >
              {error}
            </div>
          )}
          <div className="register-form-container">
            <div className="register-form-group">
              <label htmlFor="first_name">First name</label>
              <input
                type="text"
                id="first_name"
                name="first_name"
                placeholder="Enter your first name"
                required=""
                ref={Fname}
              />
            </div>
            <div className="register-form-group">
              <label htmlFor="last_name">Last name</label>
              <input
                type="text"
                id="last_name"
                name="last_name"
                placeholder="Enter your last name"
                required=""
                ref={Lname}
              />
            </div>
            <div className="register-form-group">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                id="email"
                name="email"
                ref={email}
                placeholder="Enter your Email"
                required=""
              />
            </div>
            <div className="register-form-group">
              <label htmlFor="password">Password</label>
              <input
                type="password"
                id="password"
                ref={Pass}
                name="password"
                placeholder="Enter your Password"
                required=""
              />
            </div>
            <div className="register-form-group">
              <label htmlFor="passwordCheck">Confirm Password</label>
              <input
                type="password"
                id="passwordCheck"
                name="passwordCheck"
                ref={PassCheck}
                placeholder="Confirm your Password"
                required=""
              />
            </div>
            <div className="register-form-group">
              <Link
                to="/login"
                type="button"
                className="register-submit-btn"
                onClick={handleClick}
              >
                Create Account
              </Link>
            </div>
          </div>
          <div className="register-footer">
            <p>
              Already have an account? <Link to="/login">Sign in</Link>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}

export default Register;
