import React from "react";
import { createBrowserRouter, RouterProvider } from "react-router";
import { createRoot } from "react-dom/client";
import App from "./App";
import AllAbout from "./About/AllAbout";
import { AllData } from "./Data/AllData";
import AllCart from "./Cart/AllCart";
import AllShop from "./Shop/AllShop";
import AllLogin from "./login/AllLogin";
import AllRegister from "./register/AllRegister";
import AllUser from "./User/AllUser";
import AllForget from "./Forget/AllForget";
import AllConfirm from "./confirm/AllConfirm";
import AllContact from "./contact/AllContact";
import { SearchProvider } from "./Components/SearchContext";
import AllCheckOut from "./checkout/AllCheckOut";
import AllProductDetails from "./productDetails/AllProductDetails";
let Router = createBrowserRouter([
  {
    path: "/",
    element: <App></App>,
  },
  {
    path: "/about",
    element: <AllAbout></AllAbout>,
  },
  {
    path: "/cart",
    element: <AllCart></AllCart>,
  },
  {
    path: "/shop",
    element: <AllShop></AllShop>,
  },
  {
    path: "login",
    element: <AllLogin />,
  },
  {
    path: "register",
    element: <AllRegister />,
  },
  {
    path: "user",
    element: <AllUser />,
  },
  {
    path: "reset-password",
    element: <AllForget />,
  },
  {
    path: "contact-us",
    element: <AllContact />,
  },
  {
    path: "confirmed",
    element: <AllConfirm />,
  },
  {
    path: "Checkout",
    element: <AllCheckOut />,
  },
  {
    path: "product/:id",
    element: <AllProductDetails />,
  },
]);
createRoot(document.getElementById("root")).render(
  <SearchProvider>
    <AllData>
      <RouterProvider router={Router} />
    </AllData>
  </SearchProvider>
);
