import React from 'react'
import { Link } from 'react-router'
function CategoryProductHome() {
  return (
    
        <section
            className="category-products-section d-flex justify-content-center align-items-center flex-column p-5">

            <div
                className="container feature-head   text-center d-flex justify-content-center align-items-center flex-column">
                <h2 className="f50 p-5">Categories of The Month</h2>
                <p className="p-2 mb-5">
                    Excepteur sint occaecat cupidatat non proident, sunt in
                    culpa qui
                    officia deserunt mollit anim id est laborum.
                </p>
                <div className="row">
                    <div className="col-lg-4 text-center my-md-5 my-lg-0">
                        <div className="product-card">
                            <div className="feature-card-img">
                                <img
                                    src="assets/img/category_img_01.jpg"
                                    className="card-img-top w-75"
                                    alt="shoes" />
                            </div>
                            <div className="card-body">
                                <h5 className="card-title mt-3">Watchies</h5>
                                <Link to='/shop' className="btn btn-success p-2 mt-3">Go to
                                    shop</Link>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 text-center my-md-5 my-lg-0">
                        <div className="product-card">
                            <div className="feature-card-img">
                                <img
                                    src="assets/img/category_img_02.jpg"
                                    className="card-img-top w-75"
                                    alt="shoes" />
                            </div>
                            <div className="card-body">
                                <h5 className="card-title mt-3">Shoes</h5>
                                <Link to='/shop' className="btn btn-success p-2 mt-3">Go to
                                    shop</Link>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 text-center my-md-5 my-lg-0">
                        <div className="product-card">
                            <div className="feature-card-img">
                                <img
                                    src="assets/img/category_img_03.jpg"
                                    className="card-img-top w-75"
                                    alt="Accessories" />
                            </div>
                            <div className="card-body">
                                <h5 className="card-title mt-3">Accessories</h5>
                                <Link to='/shop' className="btn btn-success p-2 mt-3">Go to
                                    shop</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

  )
}

export default CategoryProductHome